@import url(https://fonts.googleapis.com/css?family=Rubik:400, 500);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* General Styling */

body {
   background-color: #3aafa9;
   color: #feffff;
   font-family: 'Rubik', sans-serif;
   height: 100%;
   min-height: 100%;
   overflow: auto;
}

html {
   height: 100%;
}

#root {
   height: 100%;
}

.flexBoxContainer {
   display: flex;
   justify-content: center;
   align-items: center;
}

.pageContainer {
   margin-left: 10%;
   margin-right: 10%;
   margin-top: 10px;
   height: 100%;
   min-width: 600px;
}

.flexContainer {
   display: flex;
   font-weight: bold;
}

.homeContainer {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}
.homeContainer h1 {
   -webkit-animation: slideDown 1s linear 0s 1 forwards,
      fadeIn 1.25s linear 0s 1 forwards;
           animation: slideDown 1s linear 0s 1 forwards,
      fadeIn 1.25s linear 0s 1 forwards;
}

.homeContainer p {
   opacity: 0;
   -webkit-animation: slideUp 1s linear 1s 1 forwards, fadeIn 1.25s linear 1s 1 forwards;
           animation: slideUp 1s linear 1s 1 forwards, fadeIn 1.25s linear 1s 1 forwards;
}

.navBar {
   justify-content: space-between;
   margin: 0px 5px;
}

.navBar a {
   text-decoration: none;
   color: #feffff;
}

.linkContainer {
   flex: 1 1;
   justify-content: space-between;
   max-width: 200px;
}

div .skills {
   height: 100px;
   width: 100px;
}

.profilePic {
   border: 4px solid #2b7a78;
   margin-right: 20px;
}

.skillsContainer {
   display: flex;
   justify-content: center;
   align-items: center;
   background: #def2f1;
   border: 4px solid #2b7a78;
   min-height: -webkit-fit-content;
   min-height: -moz-fit-content;
   min-height: fit-content;
   -webkit-perspective: 1000px;
           perspective: 1000px;
}

.flip-container {
   -webkit-perspective: 1000px;
           perspective: 1000px;
}
/* flip the pane when hovered */
.flip-container:hover .flipper,
.flip-container.hover .flipper {
   -webkit-transform: rotateY(-180deg);
           transform: rotateY(-180deg);
}

.skillsSize {
   width: 90px;
   height: 90px;
}

/* .projectSize {
   padding-bottom: 25%;
} */

/* flip speed goes here */
.flipper {
   transition: 0.6s;
   -webkit-transform-style: preserve-3d;
           transform-style: preserve-3d;
   height: 100%;
   width: 100%;

   position: relative;
}

/* hide back of pane during swap */
.front,
.back {
   -webkit-backface-visibility: hidden;
           backface-visibility: hidden;

   position: absolute;
   top: 0;
   left: 0;
   bottom: 0;
   right: 0;
}

/* front pane, placed above back */
.front {
   z-index: 2;
   /* for firefox 31 */
   -webkit-transform: rotateY(0deg);
           transform: rotateY(0deg);
}

/* back, initially hidden pane */
.back {
   -webkit-transform: rotateY(-180deg);
           transform: rotateY(-180deg);
   color: #2b7a78;
}

.backContent {
   display: flex;
   justify-content: center;
   align-items: center;
   width: 100%;
   height: 100%;
}

.contactContainer {
   display: flex;
   background: #def2f1;
   border: 4px solid #2b7a78;
}

.contactContainer a {
   margin: 10px;
}

.mailLink {
   text-decoration: none;
   color: #17252a;
   margin-left: 5px;
}

