.projectsContainer {
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   grid-auto-rows: 1fr;
   grid-column-gap: 3%;
   grid-row-gap: 4%;
   width: 100%;
   margin-bottom: 10px;
}

.projectsContainer::before {
   width: 0px;
   padding-bottom: 100%;
   content: '';
   grid-column: 1/1;
   grid-row: 1/1;
}
.projectsContainer > *:first-child {
   grid-column: 1/1;
   grid-row: 1/1;
}

.projectSize {
   height: 100%;
}
.projectsContainer img {
   width: calc(100% - 8px);
   border: 4px solid #2b7a78;
}

.basicFlexContainer {
   display: flex;
   flex-direction: column;
   align-items: center;
}

.placeholderBack {
   display: flex;
   justify-content: center;
   align-items: center;
   background: #def2f1;
   height: 100%;
   width: 100%;
   color: #2b7a78;
}

@-webkit-keyframes slideDown {
   from {
      margin-top: 10%;
   }

   to {
      margin-top: 20%;
   }
}

@keyframes slideDown {
   from {
      margin-top: 10%;
   }

   to {
      margin-top: 20%;
   }
}

@-webkit-keyframes slideUp {
   from {
      margin-top: 10%;
   }

   to {
      margin-top: 0%;
   }
}

@keyframes slideUp {
   from {
      margin-top: 10%;
   }

   to {
      margin-top: 0%;
   }
}

@-webkit-keyframes fadeIn {
   from {
      opacity: 0;
   }

   to {
      opacity: 1;
   }
}

@keyframes fadeIn {
   from {
      opacity: 0;
   }

   to {
      opacity: 1;
   }
}
